export default {
	data() {
		return {
			showPinMask: false,
			password:'',
			id:'',
			name:'',
			state:'',
			ratio:'',
			addtime:'',
			passtime:'',
			paytime:'',
			numEVC:'',
			number:''
		};
	},

	onLoad(options) {
		this.id = options.id
		//产品详情
		uni.request({
			url: this.baseUrl + "/my-detail",
			data: {
				Id:this.id
			},
			header:{
				Authorization:uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res)
				if (this.$base1._indexOf(res.data.status)) {
					this.$base1._isLogin()
				} else if(res.data.status==1){
					this.name = res.data.data.Name
					this.ratio = res.data.data.Ratio
					this.state = res.data.data.State
					this.number = res.data.data.Number
					this.addtime= this.$base1._formatDate(res.data.data.AddTime) 
					this.passtime=this.$base1._formatDate(res.data.data.PassTime)  
					this.paytime= this.$base1._formatDate(res.data.data.PayTime)
					if(this.state==0){
						this.paytime=''
						this.passtime=''
					}
					if(this.state==3||this.state==4){
						this.paytime=''
					}
					this.numEVC= res.data.data.NumberEVC
				}else{
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					})
				}
				
			}
		})
	},
	onPullDownRefresh() {
		
	},
	methods: {
		closePinMask(){
			this.showPinMask = false
		},
		declarationForm(){
			//报单
			this.showPinMask = true
			
			
		},
		jumpToForgetPassword(){
			uni.navigateTo({
				url:"../personal/forget-pay-password"
			})
		},
		showStatus(status){
			if(status==0){
				return '未报单'
			}else if(status==1){
				return '已报单'
			}else if(status==2){
				return '已出局'
			}else if(status==3){
				return '已放行'
			}else if(status==4){
				return '违约'
			}
		},
		confirm(){
			//我的广告包
			uni.request({
				url: this.baseUrl + "/pay-product",
				data: {
					PayPassword: this.password
				},
				method:"POST",
				header:{
					Authorization:uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					 if(res.data.status==1){
						 this.showPinMask = false
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
						
					}else{
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					}
					
				}
			})
		}

	}
}
